.icon {
  position: relative;

  display: block;
  width: $icon-width;
  height: $icon-width;

  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  text-decoration: none;

  @include onDesktop {
    width: 24px;
    height: 24px;
  }

  &__tooltip {
    position: absolute;
    right: 100%;
    top: 50%;
    transform: translateY(-50%);

    display: block;
    width: max-content;
    padding-right: 20px;

    font-weight: 700;
    font-size: 12px;
    line-height: 16px;
    color: #fff;
    letter-spacing: 2px;
    text-transform: uppercase;
    opacity: 0;
    pointer-events: none;

    transition: opacity 0.3s;
  }

  &:hover &__tooltip {
    opacity: 1;
    pointer-events: all;
  }

  &--menu {
    background-image: url(../images/menu.svg);

    &:hover {
      background-image: url(../images/menu_hover.svg);
    }
  }

  &--phone {
    background-image: url(../images/phone.svg);
  }

  &--cross {
    background-image: url(../images/cross.svg);
    height: 18px;
    width: 18px;

    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    @include onDesktop {
      height: 24px;
      width: 24px;
    }
  }
}
